<style scoped lang="less">
.mineContainer {
  width: 750rpx;
  height: 80rpx;
  display: flex;
  align-items: center;
  padding: 0 20rpx;
  margin-top: 10rpx;
  box-sizing: border-box;
  background-color: #fff;
  .more {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .moreIcon{
      width: 30rpx;
      height: 30rpx;
      margin-left: 8rpx;
    }
  }
}
</style>
<template>
  <view class="mineWrap container">
    <!--清除缓存-->
    <view class="mineContainer">
      <view class="cache">清除缓存</view>
      <view class="more">
        <view>0B</view>
        <image class="moreIcon" src="../../static/mine/mine-more.png"></image>
      </view>
    </view>
    <!--帮助-->
    <view class="mineContainer" @click="toSkipHelp">
      <view class="cache">帮助</view>
      <view class="more">
        <image class="moreIcon" src="../../static/mine/mine-more.png"></image>
      </view>
    </view>
    <!--关于-->
    <view class="mineContainer" @click="toSkipAbout">
      <view class="cache">关于</view>
      <view class="more">
        <image class="moreIcon" src="../../static/mine/mine-more.png"></image>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    methods: {
      toSkipHelp () {
        uni.navigateTo({
          url: "../help/help"
        })
      },
      toSkipAbout () {
        uni.navigateTo({
          url: "../about/about"
        })
      }
    }
  }
</script>

